.nav-opened {
  position: absolute;
  z-index: 1;
  top: 0;
  right: -33px;
  display: block;
  width: 384px;
  height: 1024px;
  background-color: @basic_dark;
  box-shadow: -380px 0 5px rgba(48, 48, 48, 0.6);

  @media (max-width: @mobile-width) {
    position: absolute;
    // z-index: 1;
    top: 0;
    right: -220px;
    display: block;
    width: 800px;
    height: 10000px;
    background-color: @basic_dark;
  }
}

.nav-item-head {
  display: none;

  @media (max-width: @tablet-width) {
    display: flex;  
    justify-content: right;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 92px;
  }

  @media (max-width: @mobile-width) {
    position: relative;
    margin-bottom: 30px;
    width: 328px;
    height: 100px;
  }
}

.nav-img-head {


  @media (max-width: @tablet-width) {
    display: none;
    width: 70px;
    height: 70px;
    margin-left: 0;
    margin-top: 15px;
  }

  @media (max-width: @mobile-width) {
    display: block;
    margin-left: 0;
    margin-right: 155px;
    margin-top: 19px;
  }
}
.nav-img-head-logo {
  display: none;

  @media (max-width: @mobile-width) {
    display: block;
    width: 50px;
    height: 50px;
  }
}
.nav-button-contact-head {

  @media (max-width: @tablet-width) {
    display: block;
    margin-right: 100px;
    margin-top: 23px;
  }

  @media (max-width: @mobile-width) {
    margin-right: 50px;
    margin-top: 0;
    width: 36px;
    height: 36px;
  }
}
.nav__toggle-head {
  display: none;


  @media (max-width: @tablet-width) {
    display: block;
    content: "";
    position: absolute;
    z-index: 5;
    top: 48px;
    right: 37px;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    transform: rotate(45deg);

    &::before {
      content: "";
      position: absolute;
      z-index: 4;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
      transform: rotate(-90deg);
    }
  }

  @media (max-width: @mobile-width) {
    display: block;
    position: absolute;
    z-index: 5;
    top: 45px;
    right: -2px;
    background-color: #303030;
    width: 30px;
    height: 30px;

    &::after {
    content: "";
    position: absolute;
    z-index: 5;
    top: 0;
    right: 0;
    width: 30px;
    height: 2px;
    background-color: #FDFDFD;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 2px;
      background-color: #FDFDFD;
    }
  }
}
.nav-item-social {
  display: none;

  @media (max-width: @tablet-width) {
    display: block;
    width: 313px;
    height: 70px;
    margin-left: 36px;
    margin-top: 198px;
    color: @background_page;
    font-family: "Satoshi-Bold", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    text-transform: uppercase;
    text-align: right;


    &::after {
      content: "";
      position: absolute;
      top: 712px;
      right: 0;
      width: 100%;
      height: 1px;
      background-color: #A0A0A0;
    }
  }

  @media (max-width: @mobile-width) {
    display: none;
  }
}
.nav-item-row1 {
  display: flex;
  justify-content: space-between;
}
.nav-item-row2 {
  width: 200px;
  margin-left: 113px;
  margin-top: 33px;
  display: flex;
  justify-content: space-between;
}

